
import { NextResponse } from 'next/server';
import { GoogleGenerativeAI } from '@google/generative-ai';
import { YoutubeTranscript } from 'youtube-transcript';
import pool from '@/lib/db';
import { verifyToken } from '@/lib/auth';
import { cookies } from 'next/headers';

// Initialize Gemini API
const genAI = new GoogleGenerativeAI(process.env.GEMINI_API_KEY || '');
const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

export async function POST(req: Request) {
  try {
    // 1. Authentication Check
    const cookieStore = await cookies();
    const token = cookieStore.get('token')?.value;

    if (!token) {
      return NextResponse.json({ message: 'Unauthorized' }, { status: 401 });
    }

    const payload = verifyToken(token);
    if (!payload || !payload.id) {
      return NextResponse.json({ message: 'Invalid token' }, { status: 401 });
    }

    const userId = payload.id;

    // 2. Parse Request
    const { videoUrl } = await req.json();

    if (!videoUrl) {
      return NextResponse.json({ message: 'Video URL is required' }, { status: 400 });
    }

    // 3. Extract Video ID
    const videoIdMatch = videoUrl.match(/(?:youtube\.com\/(?:[^\/]+\/.+\/|(?:v|e(?:mbed)?)\/|.*[?&]v=)|youtu\.be\/)([^"&?\/\s]{11})/);
    const videoId = videoIdMatch ? videoIdMatch[1] : null;

    if (!videoId) {
      return NextResponse.json({ message: 'Invalid YouTube URL' }, { status: 400 });
    }

    // 4. Fetch Transcript
    let transcriptText = '';
    try {
      const transcript = await YoutubeTranscript.fetchTranscript(videoId);
      transcriptText = transcript.map(item => item.text).join(' ');
    } catch (error) {
      console.error('Transcript fetch error:', error);
      return NextResponse.json({ message: 'Failed to fetch transcript. The video might not have captions enabled.' }, { status: 400 });
    }

    if (!transcriptText) {
      return NextResponse.json({ message: 'No transcript available for this video.' }, { status: 400 });
    }

    // 5. Generate Summary with Gemini
    const prompt = `
      You are an expert academic tutor. Summarize the following YouTube video transcript into a structured set of study notes.
      
      Format the output using Markdown.
      Include the following sections:
      1. **Core Concept**: A concise explanation of the main topic (~2-3 sentences).
      2. **Detailed Breakdown**: Bullet points explaining key ideas, arguments, or steps. Use nested bullets if necessary.
      3. **Summary**: A concluding paragraph wrapping up the lesson.

      Transcript:
      ${transcriptText}
    `;

    const result = await model.generateContent(prompt);
    const summaryContent = result.response.text();

    // 6. Save to Database
    await pool.query(
      'INSERT INTO summaries (user_id, video_url, summary_content) VALUES ($1, $2, $3)',
      [userId, videoUrl, summaryContent]
    );

    return NextResponse.json({ summary: summaryContent }, { status: 200 });

  } catch (error) {
    console.error('Summarization error:', error);
    return NextResponse.json({ message: 'Internal server error while generating summary.' }, { status: 500 });
  }
}
